<template>
<!--title-->
<h1>Weather Dashboard</h1>

<!--Sidebar starts-->
<div id="app1" class="try" :class="typeof weather.main != 'undefined' && weather.main.temp > 16 ? 'warm' : ''">
    <main>
        <div class="search-box">
            <input type="text" class="search-bar" placeholder="Search..." v-model="query" @keypress="fetchWeather"/>
        </div>

        <div class="weather-wrap" v-if="typeof weather.main != 'undefined'">
            <div class="location-box">
                <div class="location">{{ weather.name }}, {{ weather.sys.country }}</div>
                <div class="date">{{ dateBuilder() }}</div>
            </div>

            <div class="weather-box">
                <div class="temp">{{ Math.round(weather.main.temp) }}°c</div>
                <div class="weather">{{ weather.weather[0].main }}</div>
            </div>

            <div class="weather-box2">
                <div class="temp-range" >Min Temp :{{Math.round(weather.main.temp_min)}}°c</div>
                <div class="temp-range" >Max Temp :{{Math.round(weather.main.temp_max)}}°c</div>
            </div>
        </div>
    </main>
</div>
<!--Sidebar ends-->
<!--Temperature Component-->
<TempChart :tempVarDt="tempVarDt" :tempVar ="tempVar"/>
<!--Temperature Highlight Component-->
<TempHighlight :speed="wea.speed" :degree="wea.degree" :visible="wea.visible" :uvi="wea.uvi"/> 
</template>

<script>

import TempChart from './components/TempChart.vue'
import TempHighlight from './components/TempHighlight.vue'

export default {
    name: 'App',
    components: {
       
        TempChart,
        TempHighlight,
    },

     data() {
        return {
            api_key: '33b3c4b52fa4a61bf67f5852dbccf425',
            url_base: 'https://api.openweathermap.org/data/2.5/',
            query: '',
            weather: {},
            fetchResponse:{},
            timeZone:'',
            tempVar:[],
            tempVarDt:[],
            
            wea:
            {
                uvi:"",
                speed:"",
                degree:"",
                visible:"",
                longitude:"",
                latitude:"",
            },
        };
    },
    methods: {
        fetchWeather(e) {
            if (e.key == "Enter") {
                fetch(`${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`)
                    .then(res => {
                        return res.json();
                    }).then(this.setResults);
            } 
        },
        setResults(results) {
            //console.warn(results);
            this.weather = results;
            this.wea.speed=results.wind.speed;
            this.wea.degree=results.wind.deg;
            this.wea.visible=results.visibility;
            this.wea.longitude=results.coord.lon;
            this.wea.latitude=results.coord.lat;
            this.uvifinder();
        },

        uvifinder:async function(){
            const url2=`https://api.openweathermap.org/data/2.5/onecall?lat=${this.wea.latitude}&lon=${this.wea.longitude}&exclude=minutely,daily,alerts&units=metric&appid=${this.api_key}`;
            const res= await fetch(url2);
            const res2= await res.json();
            //console.warn("msg"+ res2);
            this.wea.uvi=res2.current.uvi; 
            setTimeout(() =>  { fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.wea.latitude}&lon=${this.wea.longitude}&appid=${this.api_key}`)
         .then(response => response.json()).then(this.fetchResponses) } , 1000);
        
         },
  
          
      fetchResponses(fetchresponsevalue){
          
          this.fetchResponse =fetchresponsevalue;
          for (var i = 0; i < 8; i++) {
            this.tempVar.push(this.fetchResponse.hourly[i].dt); 
            this.tempVarDt.push(this.fetchResponse.hourly[i].temp);
      }
            
       
        },
        dateBuilder() {

            let d = new Date();
            let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
            let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

            let day = days[d.getDay()];
            let date = d.getDate();
            let month = months[d.getMonth()];
            let year = d.getFullYear();

            return `${day} ${date} ${month} ${year}`;
        }
    }
}
</script>

